<template>
  <div class="ner">
    <div class="text_input">
      <div class="text_title">
        <strong>输入文本:</strong>
      </div>
      <div class="text_content">
        <el-input type="textarea" :rows="10" placeholder="请输入内容" v-model="textarea">
        </el-input>
        <div class="submit">
          <el-button type="primary" style="width: 350px;margin-top: 20px;" @click="predict">提交！</el-button>
        </div>
      </div>
    </div>
    <div class="rec_result">
      <div class="rec_title" id="rec_title">
        <strong>[查看识别结果]:</strong>
      </div>
      <div class="rec_content" id="rec_content">
        <el-input type="textarea" :rows="10" placeholder="" v-model="resultArea" id='resultArea'>
        </el-input>
      </div>
    </div>

    <div id="entities">
      <div id="entities_title">
        <strong>实体百科</strong>
      </div>
      <div id="entities_content">
        <div style="margin-left: 280px;">
          <el-table :data="tableData" style="width: 70%" max-height="250" border stripe>
            <el-table-column prop="entity" label="实体" align="center">
            </el-table-column>
            <el-table-column label="操作" align="center">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="skip(scope.row)">
                  实体知识
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        textarea: '',
        resultArea: '',
        tableData: []
      }
    },
    methods: {
      predict() {
        this.$store.dispatch('kg/ner', this.textarea).then(response => {
          const data = response.obj
          let res = ''
          //          for(let i = 0;i < data.length;i++){
          //            res += data[i] +'\n'
          //          }
          const tableData = []
          for(let key in data) {
            res += key + "\n"
            if(data[key] == 1) {
              tableData.push({
                entity: key
              })
            }
          }

          if(res == '') {
            this.resultArea = '没有识别出任何实体'
          }

          this.resultArea = res
          this.tableData = tableData

        })
      },
      skip(row){
        this.$router.push("/kg/baike/" + row.entity)
      }
    }
  }
</script>

<style>
  .ner {
    /* padding: 32px;*/
    width: 100%;
  }
  
  .rec_content {
    background-color: white;
    padding: 30px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
  
  .text_content {
    background-color: white;
    padding: 30px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
  
  .text_title {
    background-color: #F0F2F5;
    padding: 10px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
  
  .submit {
    width: 100%;
    text-align: center;
  }
  
  .rec_result {
    margin-top: 20px;
  }
  
  .rec_title {
    background-color: #F0F2F5;
    padding: 10px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
  
  #entities_title {
    background-color: #F0F2F5;
    padding: 10px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
  
  #entities {
    margin-top: 20px;
  }
  
  #entities_content {
    background-color: white;
    padding: 30px;
    box-shadow: darkgrey 10px 10px 30px 5px;
    width: 100%;
  }
</style>


